import React from 'react';
import { EditorContextHelper } from '@magnolia/template-annotations';

const context = {};

function setContextProp(prop) {
  const { key, value } = prop;

  if (!value) console.error('Missing prop: ' + key);

  context[key] = value;
}

function EditablePage(props) {
  const { content, config, templateDefinitions } = props;

  setContextProp({ key: 'content', value: content });
  setContextProp({ key: 'config', value: config });

  if (EditorContextHelper.inEditor()) {
    setContextProp({ key: 'templateDefinitions', value: templateDefinitions });
  }

  console.log(context);

  // const template = config.templates[props['mgnl:template']];

  // return template ? React.createElement(template, props) : null;

  return <div>Page</div>;
}

export { EditablePage };
